.custom-button {
  min-width: 165px;
  width: auto;
  height: 50px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px 0 35px;
  font-size: 15px;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed';
  font-weight: bolder;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  
  margin-right: 5px;
  &:hover {
    background-color: white;
    color: black;
    border: 1px solid black;
  }
  &.google-button {
    background-color: rgb(62, 62, 255);
    color: #ffffff;

    &:hover {
      background-color: blue;
      border: none;
    }
  }

  &.inverted {
    background-color: white;
    color: black;
    border: 1px solid black; 
    &:hover {
      background-color: black;
      color: white;
      border: none;
    }
  }
}

